@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title {
    font-size: 80px;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}
.page {
    padding-top: 60px;
    padding-bottom: 60px;
    background-color: #F5F5F5;
}

.tile {
    width: 400px;
    min-height: 550px;
    background-color: white;
    border-radius: 20px;   
    margin: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); 
    position: relative;
}

.flex {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
}

.tile_title {
    display: inline;
    width: 360px;
    font-size: 16px;
    margin:0 10px 0 0;
}

.tile_photos_wrapper {
    width: 350px;
    height: 350px;
    overflow: hidden;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top:20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
}

.text {
    margin-left:25px;
    margin-right:25px;
}

.date {
    color: #969595;
    font-size:13px;
    margin:5px 0 10px 0;
    position: absolute;
    bottom: 0;
}

.tile_header {
    display: flex;
    align-items: center;
    margin:5px 0 5px 0;

}
.heart_icon {
    width:25px;
    height:25px;
    display: inline;
}

.heart_icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.loading-screen {
    background-color: #F5F5F5;
    height: calc(100vh - 310px);
    display: flex;
    justify-content: center;

}

.loading {
    background-color: #5089C6;
    color: white;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
.description {
    width: 360px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size:14px;
    margin:0;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
}

.description_expanded {
    width: 360px;
    font-size:14px;
    margin:0;
    font-family: 'Poppins', sans-serif;
}

.expand_button {
    background-color: white;
    border:none;
    font-family: 'Poppins', sans-serif;
    color: #969595;
    font-weight: 600;
    padding:0 0 20px 0;
    cursor: pointer;
}
.expand_button:hover {
    color: #727272;
}

.expanded_button {
    background-color: white;
    border:none;
    font-family: 'Poppins', sans-serif;
    color: #969595;
    font-weight: 600;
    padding:0 0 30px 0;
    cursor: pointer;
}
.expanded_button:hover {
    color: #727272;
}
.pg_num {
    list-style-type: none;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 18px;
    padding: 15px;
    cursor: pointer;
}

.pg_num:hover {
    background-color: #3c3c3c;
}

.pg_num_list {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: black;
    color: white;
    margin:0;
}

.active_pg_num {
    background-color: #001E6C;
}

.header {
    display: block;
    background-color: #001E6C;
    padding-bottom: 15px;
    padding-top: 15px;
    display: flex;
    align-items: center;
}
.header-text {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    margin: 0;
    text-align: left;
    margin-left: -28px;
    font-size: 20px;
}

.circle {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: #FFAA4C;
    margin-left: 15%;
}
.homepage {
    background-color: black;
    height: calc(100vh - 80px);
}

.home_title {
    margin:0;
    color:white;
    text-align: center;
    padding-top: 80px;
    font-size: 80px;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
}

.start_exploring{
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 20px;
    justify-content: center;
    padding: 20px;
    border-radius: 20px;
    width: 300px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
